.some-of{
    width: 100%;
    display: flex;
    background-color: rgba(127, 255, 212, 0);
    align-items: center;
    justify-content: space-evenly;
}
.circles{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.title{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 36px;
}
.circle{
    
    /* box-shadow: 0px 0px 2px white; */
    border-radius: 50%;
    width: 120px;
    height: 120px;

    display: flex;
    align-items: center;
    justify-content: center;
}
@media screen and (max-width: 375px) {
    .some-of{
        flex-direction: column;
    }
    .circles{
        flex-direction: column;
    }
    .circle{
        margin: 10px;
        border: 1px solid white;
    }
    .title{
        font-size: 50px;
    }
}
@media screen and (min-width: 376px)  {
    .some-of{
        flex-direction: row;
        padding-right: 157px;
        /* background-color: rgba(127, 255, 212, 0.507); */
    }
    .some-of .title{
        /* background-color: rgba(127, 255, 212, 0.507); */
        width: 90%;
        height: 100%;
    }
    .circles{
        flex-direction: row;
        /* background-color: rgba(127, 187, 255, 0.507); */
        width: 110%;
        height: 100%;
    }
    .circle{
        margin: 10px;
        box-shadow: 0px 0px 2px white;
    }
}