.what-i-2{
    background-image: linear-gradient(black 20%, #191B1E 20%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    min-height: 100vh;
}
.what-head2 .div{
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-evenly;
    width: 100%;
    height: fit-content;
    background-image: linear-gradient(black 20%, #191B1E 20%);
    height: 100%;
}
.what-head2{
    position: relative;
    background-color: rgba(127, 255, 212, 0);
    width: 100%;
    height: 300px;
}
.what-head2 .img{
    width: 600px;
    height: 300px;
    background-color: rgba(0, 255, 255, 0);
}
.what-head2 div{
    font-size: 60px;
    color: rgba(255, 255, 255, 0.863);
}
.what-body2{
    background-color: rgba(0, 255, 255, 0);
    /* width: 100%; */
    height: 100%;
    color: #eef6f8d7;
    padding: 150px 0 0 200px;
}
.what-body2 .r1{
    font-size: 40px;
    font-weight: bold;
}
.what-body2 .r2{
    font-size: 40px;
    font-weight: 100;
    font-family: 'Raleway', sans-serif;
    color: #eef6f8b9;
}
@media screen and (max-width: 377px) {
    .what-i-2{
        min-height: 140vh;
    }
    .what-head2 div{
        font-size: 50px;
        color: rgba(255, 255, 255, 0.863);
    }
    .what-head2 .div div,.what-i-2 .what-body2{
        padding: 50px;
    }
    .what-head2 .div{
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-evenly;
        width: 100%;
        height: 100%;
    }
    .what-body2 .r1{
        font-size: 53px;
        font-weight: bold;
    }
    .what-body2 .r2{
        font-size: 22px;
        font-weight: 200;
        font-family: 'Raleway', sans-serif;
        color: #eef6f8b9;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .what-body2 .r2 div{
        margin-top: 30px;
    }
}
@media screen and (min-width: 378px) and (max-width: 777px) {
    .what-head2 .div{
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-evenly;
        width: 100%;
        height: 100%;
    }
}
